.counter {
    width: 300px;
    height: 130px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.counter-num {
    font: 40px "'iransans'";
    color: #000000;
}

.counter-btn-container {
    display: flex;
    flex-grow: 1;
    justify-content: space-around;
    align-items: center;
}

.counter-btn {
    width: 70px;
    height: 35px;
    border: transparent;
    background-color: #d2d2ab;
    font: 20px "'iransans'";
    color: white;
    text-align: center;
    line-height: 35px;
    cursor: pointer;
}